:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.mainItem {
  justify-content: space-between;
  margin: 0;
  padding-right: 50px;
  list-style-type: none;
  display: flex;
}

.menuItem {
  align-items: center;
  height: 60px;
  display: flex;
  position: relative;
}

.menuItemMargin {
  margin-left: 20px;
}

.start {
  width: 40px;
  height: 40px;
  padding-left: 50px;
}

.is-active {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
  text-decoration: none;
}

.is-active:hover {
  color: var(--blue-color);
}

.menuItem:hover:after {
  background-color: var(--blue-color);
  content: "";
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.imageLink {
  align-items: center;
  height: 60px;
  display: flex;
}

